/* header section styling */
header {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	background-color: var(--white-color);
	border-bottom: 6px solid var(--green-color);
	position: relative;
	min-height: 120px;
}

header > div {
	flex: 1;
	margin: 2%;
	border: 3px solid var(--green-color);
	border-radius: 50%;
	width: 150px;
	min-height: 150px;
	/* background-image: url("/images/gospel-chukwu.jpg"); */
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	position: absolute;
	left: 0;
	top: 10px;
}

header > nav {
	/* take up the remaining space by setting flex-grow as 1 */
	flex: 4;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: flex-end;
	margin-right: 2%;
}

header > nav > ul {
	display: flex;
	flex-wrap: wrap;
	border-radius: 5px 5px 0 0;
	padding: 5px;
	justify-content: space-between;
	background-color: var(--green-color);
}

header > nav > ul > li {
	display: inline;
}

header > nav > ul > li > a {
	background-color: var(--green-color);
	display: block;
	padding: 10px 30px;
	color: var(--white-color);
}

header > nav > ul > li > a:hover {
	background-color: var(--white-color);
	color: var(--green-color);
	border-radius: 5px 5px 0 0;
}

/* media query for screens below 760px */
@media screen and (max-width: 760px) {
	/* change the header flex direction to column instead of row */
	header {
		flex-direction: column;
		align-items: center;
		min-height: 200px;
	}

	header > div {
		position: relative;
		top: 0;
	}

	header > nav {
		margin-right: 0;
	}

	header > nav > ul > li {
		width: 100%;
		text-align: center;
	}
}
